"use client"

import { useState } from "react"
import { motion } from "framer-motion"

interface Language {
    name: string
    proficiency: number // 0-100
    level: string // e.g., "Native", "Fluent", "Intermediate", "Basic"
    flag?: string // emoji flag
}

const languages: Language[] = [
    { name: "English", proficiency: 95, level: "Fluent", flag: "ðŸ‡¬ðŸ‡§" },
    { name: "Urdu", proficiency: 100, level: "Native", flag: "ðŸ‡µðŸ‡°" },
    { name: "Arabic", proficiency: 70, level: "Intermediate", flag: "ðŸ‡¸ðŸ‡¦" },
    // Add more languages as needed
]

export default function LanguagePage() {
    const [hoveredIndex, setHoveredIndex] = useState<number | null>(null)

    return (
        <div style={{
            minHeight: '100vh',
            background: '#282c20',
            color: 'white',
            padding: '40px 20px'
        }}>
            {/* Header */}
            <div style={{
                maxWidth: '1400px',
                margin: '0 auto',
                marginBottom: '60px'
            }}>
                <motion.div
                    initial={{ opacity: 0, y: -20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.6 }}
                    style={{ textAlign: 'center', marginBottom: '60px' }}
                >
                    <h1 style={{
                        fontSize: 'clamp(3rem, 8vw, 6rem)',
                        fontWeight: '900',
                        textTransform: 'uppercase',
                        letterSpacing: '-3px',
                        marginBottom: '20px',
                        background: 'linear-gradient(135deg, white 0%, #c8ff00 100%)',
                        WebkitBackgroundClip: 'text',
                        WebkitTextFillColor: 'transparent',
                        backgroundClip: 'text'
                    }}>
                        Languages
                    </h1>
                    <p style={{
                        fontSize: '1.25rem',
                        color: 'rgba(255, 255, 255, 0.7)',
                        maxWidth: '600px',
                        margin: '0 auto',
                        lineHeight: '1.6'
                    }}>
                        Communication across cultures, connecting through words
                    </p>
                </motion.div>

                {/* Language Grid */}
                <div style={{
                    display: 'grid',
                    gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))',
                    gap: '40px',
                    maxWidth: '1200px',
                    margin: '0 auto',
                    marginBottom: '80px'
                }}>
                    {languages.map((language, index) => (
                        <motion.div
                            key={language.name}
                            initial={{ opacity: 0, y: 30 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ duration: 0.5, delay: index * 0.1 }}
                            onMouseEnter={() => setHoveredIndex(index)}
                            onMouseLeave={() => setHoveredIndex(null)}
                            style={{
                                background: hoveredIndex === index
                                    ? 'rgba(58, 63, 50, 0.6)'
                                    : 'rgba(58, 63, 50, 0.4)',
                                borderRadius: '20px',
                                padding: '40px',
                                border: hoveredIndex === index
                                    ? '2px solid rgba(200, 255, 0, 0.6)'
                                    : '2px solid rgba(200, 255, 0, 0.2)',
                                transition: 'all 0.3s ease',
                                backdropFilter: 'blur(10px)',
                                transform: hoveredIndex === index ? 'translateY(-5px)' : 'translateY(0)',
                                boxShadow: hoveredIndex === index
                                    ? '0 20px 40px rgba(0, 0, 0, 0.3)'
                                    : '0 4px 20px rgba(0, 0, 0, 0.2)',
                                position: 'relative',
                                overflow: 'hidden'
                            }}
                        >
                            {/* Flag Emoji */}
                            {language.flag && (
                                <div style={{
                                    fontSize: '4rem',
                                    marginBottom: '20px',
                                    textAlign: 'center',
                                    filter: hoveredIndex === index ? 'none' : 'grayscale(50%)',
                                    transition: 'filter 0.3s ease'
                                }}>
                                    {language.flag}
                                </div>
                            )}

                            {/* Language Name */}
                            <h3 style={{
                                fontSize: '2rem',
                                fontWeight: '800',
                                marginBottom: '10px',
                                textAlign: 'center',
                                color: '#c8ff00',
                                textTransform: 'uppercase',
                                letterSpacing: '-1px'
                            }}>
                                {language.name}
                            </h3>

                            {/* Level */}
                            <p style={{
                                fontSize: '1rem',
                                color: 'rgba(255, 255, 255, 0.7)',
                                textAlign: 'center',
                                marginBottom: '30px',
                                textTransform: 'uppercase',
                                letterSpacing: '1px',
                                fontWeight: '600'
                            }}>
                                {language.level}
                            </p>

                            {/* Circular Progress */}
                            <div style={{
                                display: 'flex',
                                justifyContent: 'center',
                                alignItems: 'center',
                                marginBottom: '20px'
                            }}>
                                <svg width="150" height="150" style={{ transform: 'rotate(-90deg)' }}>
                                    {/* Background Circle */}
                                    <circle
                                        cx="75"
                                        cy="75"
                                        r="65"
                                        fill="none"
                                        stroke="rgba(200, 255, 0, 0.1)"
                                        strokeWidth="10"
                                    />
                                    {/* Progress Circle */}
                                    <motion.circle
                                        cx="75"
                                        cy="75"
                                        r="65"
                                        fill="none"
                                        stroke="#c8ff00"
                                        strokeWidth="10"
                                        strokeLinecap="round"
                                        initial={{ strokeDasharray: "0 408.4" }}
                                        animate={{
                                            strokeDasharray: `${(language.proficiency / 100) * 408.4} 408.4`
                                        }}
                                        transition={{ duration: 1, delay: index * 0.1 + 0.3, ease: "easeOut" }}
                                    />
                                    {/* Percentage Text */}
                                    <text
                                        x="75"
                                        y="75"
                                        textAnchor="middle"
                                        dy="7"
                                        fontSize="24"
                                        fontWeight="bold"
                                        fill="white"
                                        style={{ transform: 'rotate(90deg)', transformOrigin: '75px 75px' }}
                                    >
                                        {language.proficiency}%
                                    </text>
                                </svg>
                            </div>

                            {/* Progress Bar */}
                            <div style={{
                                width: '100%',
                                height: '8px',
                                background: 'rgba(200, 255, 0, 0.1)',
                                borderRadius: '4px',
                                overflow: 'hidden'
                            }}>
                                <motion.div
                                    initial={{ width: 0 }}
                                    animate={{ width: `${language.proficiency}%` }}
                                    transition={{ duration: 1, delay: index * 0.1 + 0.5, ease: "easeOut" }}
                                    style={{
                                        height: '100%',
                                        background: 'linear-gradient(90deg, #c8ff00 0%, #a0d000 100%)',
                                        borderRadius: '4px'
                                    }}
                                />
                            </div>
                        </motion.div>
                    ))}
                </div>

                {/* Back Button */}
                <div style={{
                    display: 'flex',
                    justifyContent: 'center',
                    marginTop: '60px'
                }}>
                    <a
                        href="/"
                        style={{
                            display: 'inline-flex',
                            alignItems: 'center',
                            gap: '10px',
                            background: 'rgba(40, 44, 32, 0.95)',
                            color: '#c8ff00',
                            textDecoration: 'none',
                            fontWeight: '700',
                            fontSize: '0.95rem',
                            padding: '14px 28px',
                            borderRadius: '8px',
                            border: '2px solid rgba(200, 255, 0, 0.4)',
                            transition: 'all 0.3s ease',
                            backdropFilter: 'blur(10px)',
                            textTransform: 'uppercase',
                            letterSpacing: '1px'
                        }}
                        onMouseEnter={(e) => {
                            e.currentTarget.style.background = '#c8ff00'
                            e.currentTarget.style.color = '#282c20'
                            e.currentTarget.style.transform = 'translateY(-2px)'
                            e.currentTarget.style.boxShadow = '0 10px 30px rgba(200, 255, 0, 0.3)'
                        }}
                        onMouseLeave={(e) => {
                            e.currentTarget.style.background = 'rgba(40, 44, 32, 0.95)'
                            e.currentTarget.style.color = '#c8ff00'
                            e.currentTarget.style.transform = 'translateY(0)'
                            e.currentTarget.style.boxShadow = 'none'
                        }}
                    >
                        <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5">
                            <path d="M19 12H5M12 19l-7-7 7-7" />
                        </svg>
                        Back to Home
                    </a>
                </div>
            </div>
        </div>
    )
}
